import * as _ from "lodash";
import * as tv4 from "tv4";

// 	
// $schema	$schema  v4 
// title	
// description	
// type	type  JSON  JSON 
// properties	 JSON 
// required	
// minimum	
// exclusiveMinimum	 "exclusiveMinimum"  true "minimum" 
// maximum	
// exclusiveMaximum	 "exclusiveMinimum"  true "maximum" 
// multipleOf	 "multipleOf" 
// maxLength	
// minLength	
// pattern	

const _schema: string = "http://json-schema.org/draft-04/schema#";

abstract class BaseTypeBuilder {
    protected data: tv4.JsonSchema;

    constructor() { this.data = {}; }

    build(type: JsonSchemaTypes = "string", title?: string, description?: string): BaseTypeBuilder {
        this.data = _.extend({
            title: title || "",
            description: description || "",
            type: type
        }, this.data || {});

        return this;
    };

    properties(props: { [key: string]: tv4.JsonSchema; }): JsonSchemaBuilder {
        this.data = _.extend({
            properties: props,
        }, this.data || {});

        return this;
    }

    items(items: tv4.JsonSchema | tv4.JsonSchema[]): JsonSchemaBuilder {
        this.data = _.extend({
            items: items,
        }, this.data || {});

        return this;
    }
    schema(schema: string = _schema, id: string = _schema): BaseTypeBuilder {
        this.data = _.extend({
            "$schema": schema,
            id: id
        }, this.data || {});

        return this;
    }

    required(...keys) {
        this.data = _.extend({
            required: keys
        }, this.data || {});

        return this;
    }

    toValue(): tv4.JsonSchema {
        return this.data;
    }
}


export class JsonSchemaBuilder extends BaseTypeBuilder {
    constructor() {
        super();
    }

    static init(): JsonSchemaBuilder {
        return new JsonSchemaBuilder();
    }
}


